import popSpec from '../../components/popSpec.vue';
import goodsHeader from './goodHeader.vue';
import interfaces from '../../utils/interfaces.js';
export default {
	components: {
		goodsHeader,
		popSpec
	},
	data() {
		return {
			isKeep: false,
			swiperIndex: 0,
			goodsData: {
				swiperList: [],
				spec: [],
				comment: [{
					face: "",
					content: "",
					username: ""
				}]
			},
			goodsInfo: {
				name: '',
				price: 0,
				number: 0,
				spec: ""
			},
			spaceInfo: {
				showSpace: false
			}
		}
	},
	onLoad(option) {
		this.goodsInfo = JSON.parse(option.goodsInfo)
		console.log(this.goodsInfo);
		this.initData()
	},
	methods: {
		initData() {
			this.request({
				url: interfaces.getGoods,
				success: res => {
					console.log(res.data, 'data');
					this.goodsData = res.data
				}
			})
		},
		handleSwiper(e) {
			this.swiperIndex = e.detail.current;
		},
		showPopSpec() {
			this.spaceInfo.showSpace = true
		},
		hidePopSpec() {
			this.spaceInfo.showSpace = false
		},
		handleSelectSpec(spec) {
			this.goodsInfo.spec = spec
		},
		sub() {
			if (this.goodsInfo.number <= 1) {
				return
			}
			this.goodsInfo.number--
		},
		add() {
			this.goodsInfo.number++;
			console.log(typeof(this.goodsInfo.number));
		},
		showAllComment() {
			try {
				uni.setStorageSync('comments', this.goodsData.comment)
				uni.navigateTo({
					url: './ratings'
				})
			} catch (e) {
				//TODO handle the exception
				console.log(e);
			}
		},
		keep() {
			this.isKeep = !this.isKeep;
		},
		joinCart() {
			// console.log(this.goodsInfo);

			uni.getStorage({
				key: "goodsList",
				success: res => {
					let goodsList = res.data;
					console.log(goodsList);
					let isExist = false;

					goodsList.forEach(goods => {
						if (goods.goods_id == this.goodsInfo.goods_id && goods.spec == this.goodsInfo.spec) {
							goods.number = parseInt(goods.number) + parseInt(this.goodsInfo.number);
							isExist = true
						}
					})
					
					if(!isExist) {
						goodsList.push(this.goodsInfo);
					}
					this.setGoodsList(goodsList);
				},
				fail: (err) => {
					let goodsList = [];
					console.log(typeof(this.goodsInfo.number));
					goodsList.push(this.goodsInfo);
					this.setGoodsList(goodsList);
				}
			})
		},
		setGoodsList(goodsList) {
			uni.setStorage({
				key: "goodsList",
				data: goodsList,
				success() {
					uni.showToast({
						icon: "success",
						title: "添加购物车成功"
					})
				}
			})
		}
	}
}
